.guide__item {
  display: flex;
  align-items: center;
}
.guide__item::before {
  content: "";
  min-width: 60px;
  height: 60px;
  background-color: #68b738;
  background-repeat: no-repeat;
  background-position: center;
  margin-right: 20px;
}
