  background-position: 17px 16px;
  background-image: url("../img/sprite.svg#icon__leaf");
}
@media (min-width: 768px) {
  .guide__item--leaf::before {
    background-position: 28px 25px;
  }
}
.guide__item--water::before {
  background-position: 17px 16px;
  background-image: url("../img/sprite.svg#icon__water");
}
@media (min-width: 768px) {
  .guide__item--water::before {
    background-position: 28px 25px;
  }
}
.guide__item--food::before {
  background-position: 17px 16px;
  background-image: url("../img/sprite.svg#icon__food");
}
@media (min-width: 768px) {
  .guide__item--food::before {
    background-position: 28px 25px;
  }
}
.guide__item--time::before {
  background-position: 17px 16px;
  background-image: url("../img/sprite.svg#icon__time");
}
@media (min-width: 768px) {
