  font-weight: 400;
  color: #000000;
  margin: 0;
  margin-bottom: 36px;
}
.other-products__title::before {
  content: "";
  width: calc(50% - 160px);
  height: 15px;
  border-bottom: 2px solid #68b738;
}
.other-products__title::after {
  content: "";
  width: 10px;
  height: 15px;
  border-bottom: 2px solid #68b738;
  flex-grow: 1;
}
@media (min-width: 768px) {
